.header {
	border-bottom: 4px solid $primary-color;
  padding: 10px 0px 20px 0px;
	background-color: #fff;
	@include breakpoint(sm2) {
		padding: 5px 0px 10px 0px;
    position: fixed;
    width: 100%;
    z-index: 100;
	}
	#header-left {
		text-align: left;
    padding-top: 10px;
		@include breakpoint(sm2) {
			padding-top: 5px;
		}
		a {
			cursor:pointer;
		}
		img {
			max-width: 120px;
			display:inline-block;
			@include breakpoint(sm2) {
				max-width: 70px;
			}
		}
	}
	#header-right {
		#header-top-right {
			text-align:right;
			#language-select {
				&:before {
					content: "";
					background-image: url(/library/images/language-icon.png);
					background-size: 20px 20px;
					background-position: center;
					position: relative;
					display: inline-block;
					height: 20px;
					width: 20px;
					vertical-align: top;
				}
				select {
					display: inline-block;
					vertical-align: middle;
					border: 2px solid #939598;
					background-color: #FFFEFC;
					border-radius: 4px;
					padding: 2px;
					color: #004b8d;
					cursor: pointer;
					font-size: 8pt;
					max-width: 100%;
				}
			}
		}
		#header-bottom-right {
			margin-top: 20px;
			@include breakpoint(sm2) {
				text-align:right;
				margin-top: 13px;
				margin-right: 20px;
			}
		}
		i {
			font-size: 24pt;
			@include breakpoint(sm2) {
				font-size: 18pt;
			}
		}
		#main-navigation {
			text-align:right;
			@include breakpoint(sm2) {
				display:none;
			}
			> ul {
				list-style-type:none;
				padding-left: 0px;
				> li {
					display:inline-block;
					vertical-align:bottom;
					padding: 20px 0px;
					&:last-child {
						> a {
							padding-right: 0px;
						}
					}
					> a {
						padding: 20px;
						text-transform: uppercase;
						text-decoration:none;
						font-weight: bold;
						cursor:pointer;
						@include breakpoint(md) {
							padding: 20px 15px;
							font-size: 10pt;
						}
						&:hover {
							color:$secondary-color;
						}
					}
				}
				li a.menu-item {
					&:before {
						font-family: FontAwesome;
						font-style: normal;
						font-weight: normal;
						text-decoration: inherit;
						font-size:12pt;
						position: relative;
						margin-right:8px;
					}
				}
				li a.menu-item-home {
					&:before {
						content: "\f015";
					}
				}
				li a.menu-item-login {
					&:before {
						content: "\f090";
					}
				}
				li a.menu-item-profile {
					&:before {
						content: "\f007";
					}
				}
				li a.menu-item-faq {
					&:before {
						content: "\f059";
					}
				}
				li a.menu-item-notices {
					&:before {
						content: "\f0f3";
					}
					#notice-count {
						display:none;
						&.notice-count-load {
							display:inline-block;
						}
					}
				}
				li a.menu-item-principles {
					&:before {
						content: "";
						background-image: url("/library/images/9gp-mini_logo.png");
						background-size:20px 20px;
						background-position:center;
						position: relative;
						margin-right: 8px;
						display: inline-block;
						height: 20px;
						width: 20px;
						vertical-align: top;
						@include breakpoint(md) {
							background-size:18px 18px;
							height: 18px;
							width: 18px;
						}
					}
					&:hover {
						&:before {
							background-image: url("/library/images/9gp-mini_logo_hover.png");
						}
					}
				}
				li a.menu-item-logout {
					&:before {
						content: "\f08b";
						font-family: FontAwesome;
						font-style: normal;
						font-weight: normal;
						text-decoration: inherit;
						font-size:12pt;
						position: relative;
						margin-right:8px;
					}
				}
			}
		}
		#mobile-menu-button {
			@include breakpoint(md2) {
				display:none;
			}
			@include breakpoint(sm2) {
				display:inline-block;
			}
		}
	}
	#mobile-menu {
		display:none;
		@include breakpoint(md2) {
			display:none;
		}
		&.mobile-menu-load {
			display:block;
		}
	}
	li a {
		cursor:pointer;
	}
	&#admin-header {
