				vertical-align: middle;
				margin-left: 30px;
				font-size: 24pt;
				@include breakpoint(sm) {
					font-size: 18pt;
					margin-left: 20px;
				}
				@include breakpoint(xs) {
					font-size: 14pt;
					margin-left: 15px;
				}
				@include breakpoint(xxs) {
					font-size: 12pt;
					margin-left: 5px;
				}
			}
		}
		#header-right {
