							vertical-align: top;
							@include breakpoint(md) {
								background-size:18px 18px;
								height: 18px;
								width: 18px;
							}
						}
						&:hover {
							&:before {
								background-image: url("/library/images/9gp-mini_logo_hover.png");
							}
						}
					}
				}
			}
		}
		#mobile-menu-button {
			a {
				color:#fff;
			}
		}
	}
}