//Font Addins
	@font-face {
	font-family: 'HelveticaNeueLTStd';
	src: url(/library/fonts/HelveticaNeueLTStd-Lt.otf),
	url(/library/fonts/HelveticaNeueLTStd-Lt.otf) format('truetype');
	}
	@font-face {
		font-family: "HelveticaNeueLTStd";
		src: url(/library/fonts/HelveticaNeueLTStd-Bd.otf),
		url(/library/fonts/HelveticaNeueLTStd-Bd.otf) format('truetype');
		font-weight: bold;
	}
	
// Variable Declarations
	$primary-color: #004b8d;
	$secondary-color: #939598;
	$main-background-color:#fff;
	$primary-font: 'Montserrat', Helvetica, Arial, sans-serif;
	$heading-font: 'Montserrat', Helvetica, Arial, sans-serif;
	$base-font-size:12pt;

// Mixin Declarations
	@mixin home-button($color) {
		background-color:$color;
		color:#fff;
		border: 2px solid $color;
		&:hover {
			background-color:#fff;
			border:2px solid $color;
			color:$color;
			text-decoration:none;
			+ .tooltip-container {
				background-color: $color;
				color:#fff;
				overflow-y: hidden;
				max-height: 500px;
				opacity: 1;
			}
		}
		+ .tooltip-container {
			color:#fff;
			opacity: 0;
			&:hover {
				background-color: #fff;
				color:$color;
				cursor:help;
				max-height: 500px;
				opacity: 1;
			}
		}
	}
	@mixin button($color) {
		a {
			border:2px solid $color;
			color:$color;
			&:hover {
				background-color:$color;
				color:#fff;
			}
		}
	}
	@mixin form-element() {
		border: 2px solid #939598;
		background-color: #FFFEFC;
		border-radius: 4px;
		padding: 5px;
		color:$primary-color;
}

// Beginning of Body Styles
	body    {
		font-family: $primary-font;
		font-weight:400;
		font-size:$base-font-size;
		text-rendering: optimizeLegibility;
		-webkit-font-smoothing: antialiased;
		line-height: 1.42857143;
		color: #231f20;
		background-color: $main-background-color;
	}
	h1, h2, h3, h4, h5, h6	{
		font-family: $heading-font;
		font-weight:400;
		color: $primary-color;
		line-height:1.2;
		margin-top:0px;
		letter-spacing: 1px;
		text-rendering: optimizeLegibility;
		-webkit-font-smoothing: antialiased;
		font-weight:900;
	}
	h1 {
		font-size: 40pt;
		margin-bottom: 30px;
		@include breakpoint(sm2) {
			font-size: 30pt;
		}
		@include breakpoint(xxs) {
			font-size: 26pt;
		}
	}
	h2 {
		font-size: 32pt;
		margin-bottom: 20px;
		@include breakpoint(sm2) {
			font-size: 26pt;
		}
		@include breakpoint(xxs) {
			font-size: 22pt;
		}
	}
	h3 {
		font-size: 24pt;
    margin-bottom: 20px;
		@include breakpoint(sm2) {
			font-size: 20pt;
		}
		@include breakpoint(xxs) {
			font-size: 16pt;
		}
	}
	h4 {
		font-size: 18pt;
		margin-bottom: 20px;
		@include breakpoint(sm2) {
			font-size: 14pt;
		}
		@include breakpoint(xxs) {
			font-size: 11pt;
		}
	}
	h5 {
		font-size: 14pt;
		margin-bottom: 10px;
		@include breakpoint(sm2) {
			font-size: 12pt;
		}
		@include breakpoint(xxs) {
			font-size: 8pt;
		}
	}
	h6 {
		font-size: 12pt;
	}
	p {
		margin: 0 0 10px;
	}
	ul,
	ol {
		margin-top: 0;
		margin-bottom: 10px;
	}
	ul ul,
	ol ul,
	ul ol,
	ol ol {
		margin-bottom: 0;
	}
	a	{
		color:$primary-color;
		&:hover {
			color:$secondary-color;
			text-decoration:none;
		}
		&:active, &:focus {
			outline: none!important;
			text-decoration:none;
		}
	}
	img {
		vertical-align: middle;
		display: block;
		width: 100%;
		max-width: 100%;
		height: auto;
	}
	form {
		margin:0px;
	}
	//Preloads image hover for background-image hovers
	body:after{
		display:none;
		content: url(/library/images/9gp-mini_logo_hover.png);
	 }