//Global Styles
.route-debug {
	display:none!important;
}
	.main-container {
		padding:60px 0px;
		position:relative;
		@include breakpoint(sm) {
			padding:160px 0px 40px 0px;
		}
		@include breakpoint(xs) {
			padding:140px 0px 40px 0px;
		}
		ul {
			li {
				margin-bottom:20px;
				&:last-child {
					margin-bottom:0px;
				}
			}
		}
		.form-content {
			text-align:center;
	    position: relative;
		}
		#dover-dots-top {
			position:absolute;
			width:100%;
			max-width: 755px;
			height: 425px;
			top:0;
			right:0px;
			z-index: -1;
			background-image: url('/library/images/background-dots-top.png');
			background-position:center;
			background-repeat: no-repeat;
			opacity:.5;
		}
		#dover-dots-bottom {
			position:absolute;
			width:100%;
			max-width: 755px;
			height: 425px;
			bottom:0;
			left:0px;
			z-index: -1;
			background-image: url('/library/images/background-dots-bottom.png');
			background-position:center;
			background-repeat: no-repeat;
			opacity:.5;
		}
		.form-required {
			color:#FF2419;
		}
		.select-all {
			font-size: 8pt;
			display: block;
		}
		.errors-container  {
			color: #e03c31;
			font-weight: bold;
			font-size: 16pt;
			&.errors-show {
				margin-bottom:20px;
				display:block;
			}
		}
	}
	
//Buttons used around the site
	.dover-buttons-section {
		text-align:center;
		padding-top: 15px;
		.dover-buttons {
			padding: 10px 0px;
			display: inline-block;
			font-weight:bold;
			a {
				padding:10px 20px 9px 20px;
				border-radius:5px;
				cursor:pointer;
				&:hover {
					text-decoration:none;
				}
			}
			&#red-button {
				@include button(#C8002F);
			}
			&#blue-button {
				@include button($primary-color);
			}
			&#green-button {
				@include button(#00935B);
			}
			&#purple-button {
				@include button(#2D009C);
			}
		}
		input[type="submit"] {
			border:2px solid $primary-color;
			color:$primary-color;
			padding:10px 20px;
			border-radius:5px;
			cursor:pointer;
			background-color:#fff;
			font-weight:bold;
			&:hover {
				background-color:$primary-color;
				color:#fff;
			}
		}
		.submit-form input[type="submit"] {
			background-color:transparent;
			border:2px solid green;
			color:green;
			padding: 8px 20px;
			border-radius: 5px;
			&:hover {
				background-color:green;
				color:#fff;
			}
		}
		input[type="button"] {
			background-color:transparent;
			border:2px solid $primary-color;
			color:$primary-color;
			padding: 8px 20px;
			border-radius: 5px;
			&:hover {
				background-color:$primary-color;
				color:#fff;
			}
		}
	}
	
//Form Element Styling
	.form-input-label {
		margin-bottom:15px;
		font-size:16pt;
		color:$primary-color;
		font-weight: 500;
		@include breakpoint(xs) {
			margin-bottom:10px;
			font-size:14pt;
		}
	}
	.form-input {
		position: relative;
		margin-bottom:15px;
		input {
			@include form-element();
		}
		select {
			@include form-element();
			cursor: pointer;
			max-width: 100%;
			optgroup {
				font-weight:bold;
			}
		}
		textarea {
			@include form-element();
			max-width: 100%;
		}
		form.dropzone {
			max-width:481px;
			margin: 0 auto;
			margin-bottom: 30px;
		}
		&.datetime {
			max-width: 481px;
			margin: 0 auto 15px auto;
			.datetime-text {
				margin-bottom: 15px;
				font-weight: 600;
			}
			.datetime-link {
				padding: 10px 0px;
				a {
					padding: 10px;
					background-color: #FFFEFC;
					color: $primary-color;
					border: 2px solid #939598;
					text-transform: uppercase;
					border-radius: 4px;
					font-weight: 500;
					font-size:10pt;
					&:hover {
						text-decoration:none;
						background-color: $primary-color;
						color: #fff;
						border: 2px solid $primary-color;
					}
				}
			}
			.datetime-dropdown-menu {
				margin: 0px;
				padding: 10px 5px;
			}
		}
	}
	.form-radio-container {
		display: inline-block;
		text-align: left;
		.form-radio-buttons-container {
			display: inline-block;
			vertical-align:middle;
			position: relative;
			.form-radio-button {
				display:inline-block;
				.form-radio-buttons {
					position: absolute;
					width:100%;
					height: 100%;
					z-index:100;
					left:0;
					opacity: 0;
					cursor:pointer;
				}
				.form-radio-buttons:checked + .form-radio-check:after {
					content: "\f00c";
					font-family: FontAwesome;
					font-style: normal;
					font-weight: normal;
					text-decoration: inherit;
					color:$primary-color;
					font-size:16pt;
					position: absolute;
					top: -3px;
					left: 2px;
				}
				.form-radio-check {
					border:4px solid #e6e6e6;
					height:30px;
					width:30px;
					display:inline-block;
					vertical-align:middle;
					margin-right: 10px;
					position: relative;
					@include breakpoint(xs) {
						margin-right: 5px;
					}
				}
			}
			.form-radio-label {
				display:inline-block;
				vertical-align:middle;
				max-width:320px;
				@include breakpoint(xs) {
					font-size: 11pt;
					max-width:84%;
				}
			}
		}
		.form-sub-section {
			padding-left: 20px;
			margin-left: 15px;
			margin-top: 10px;
			margin-bottom: 10px;
			border-left: 2px solid $primary-color;
			@include breakpoint(xs) {
				padding-left: 10px;
			}
			.form-input-label {
				font-size: 12pt;
				margin-bottom: 10px;
				padding-top: 5px;
				@include breakpoint(xs) {
					font-size: 11pt;
				}
			}
			.form-radio-label {
				@include breakpoint(xs) {
					max-width:72%;
				}
			}
			.form-input {
				margin-bottom: 5px;
				margin-left: 10px;
				@include breakpoint(xs) {
					margin-bottom: 10px;
				}
			}
		}
	}
	
//Submit Page
	#submit-content {
		.form-input-submit-section {
			margin-bottom: 20px;
			@include breakpoint(xxs) {
				margin-bottom: 10px;
			}
			&.form-input-submit-w-sub-section {
				margin-bottom:5px;
				@include breakpoint(xxs) {
					margin-bottom:0px;
				}
			}
			.form-input-submit-label {
				display: inline-block;
				vertical-align: middle;
				font-weight: 500;
				font-size: 14pt;
				width: 49%;
				text-align: right;
				padding-right: 15px;
				@include breakpoint(xxs) {
					font-size:11pt;
					padding-right: 0px;
				}
			}
			.form-input-submit-text {
				display: inline-block;
				vertical-align: middle;
				font-weight:bold;
				width: 49%;
				text-align: left;
				padding-left: 15px;
				p {
					font-weight:300;
					font-size: 14pt;
					margin-bottom:0px;
					@include breakpoint(xxs) {
						font-size:10pt;
						padding-left: 5px;
					}
				}
				ul {
					padding-left:30px;
					margin-bottom:0px;
					li {
						font-weight:300;
						font-size: 12pt;
						margin-bottom:0px;
						@include breakpoint(xxs) {
							font-size:10pt;
							padding-left: 5px;
						}
					}
				}
				h3 {
					font-size: 16pt;
					font-weight: 700;
					margin-bottom: 10px;
				}
				h4 {
					font-size: 14pt;
					font-weight: 700;
					margin-bottom: 5px;
				}
				h5 {
					font-size: 12pt;
					font-weight: 700;
					margin-bottom: 5px;
				}
				.sub-section {
					padding-left: 10px;
					margin-left: 10px;
					border-left: 2px solid #e6e7e8;
					margin-bottom: 10px;
					p {
						font-size:12pt;
					}
				}
				.sub-sub-section {
					padding-left: 10px;
					margin-left: 10px;
					border-left: 2px solid #e6e7e8;
					margin-bottom: 10px;
					margin-top: 5px;
					p {
						font-size:12pt;
					}
				}
				.submit-safety-audit-areas {
					margin-bottom:20px;
				}
			}
		}
		.form-input-submit-sub-section {
			margin-bottom:20px;
			@include breakpoint(xxs) {
				margin-bottom:10px;
			}
			.form-input-submit-section {
				margin-bottom: 0px;
				.form-input-submit-label {
					font-size:11pt;
					@include breakpoint(xxs) {
						font-size:9pt;
					}
				}
				.form-input-submit-text {
					p {
						font-size:11pt;
						padding-left: 7px;
						@include breakpoint(xxs) {
							font-size:9pt;
						}
					}
				}
				&.form-input-submit-sub-sub-section {
					.form-input-submit-text {
						p {
							padding-left: 14px;
						}
					}
				}
			}
		}
	}
// Dropzone
	.dropzone-container {
		padding-bottom: 30px;
    color: #939598;
    text-align: center;
		position:relative;
		.dropzone-no-files {
			max-width: 481px;
			margin: 0 auto;
		}
		.dropzone-previews {
			width: 100%;
			max-width: 481px;
			display: inline-block;
			.dropzone-wait {
				display:none;
				img {
					max-width:80px;
					margin: 0 auto;
					margin-bottom: 20px;
				}
			}
			&.loading {
				.dropzone-no-files {
					display:none;
				}
				.dropzone-wait {
					display:block;
					margin-bottom: 20px;
				}
			}
		}
	}
	.dropzone-preview {
		position: relative;
		display:inline-block;
    vertical-align: middle;
    margin-right: 10px;
    padding: 20px 10px;
		.success-button {
			position:absolute;
			top:5px;
			left:0;
	    font-size: 20pt;
			color:#00935B;
		}
		.remove-button {
			position:absolute;
			top:5px;
			right:0;
	    font-size: 20pt;
			cursor: pointer;
			color:#C8002F;
			opacity:0;
			transition:.3s all ease-in-out;
			&:hover {
				color:#b50223;
			}
		}
		.dropzone-file-image {
			max-width: 130px;
		}
		&:hover {
			.remove-button {
				opacity:1;
			}
		}
	}


//Home Page
	.home-buttons-container {
		max-width:920px;
		margin:0 auto;
		.home-buttons-row {
			margin-bottom: 15px;
			@include breakpoint(xs) {
				margin-bottom: 0px;
			}
			.home-buttons {
				position:relative;
				display:inline-block;
				vertical-align: top;
				width:31.8%;
				text-align:center;
				height: 150px;
				font-size: 14pt;
				font-weight: 500;
				text-transform: uppercase;
				text-decoration:none;
				margin-right: 10px;
				@include breakpoint(sm2) {
					height: 135px;
				}
				@include breakpoint(sm) {
					margin-right: 5px;
				}
				@include breakpoint(xs) {
					width:50%;
					margin-right:0px;
					margin-bottom:10px;
				}
				@include breakpoint(xxs) {
					width:265px;
					height: 100px;
				}
				a {
					display: block;
					height: 100%;
					&#red-button {
						@include home-button(#C8002F);
					}
					&#blue-button {
						@include home-button($primary-color);
					}
					&#green-button {
						@include home-button(#00935B);
					}
					&#purple-button {
						@include home-button(#2D009C);
					}
				}
				.home-buttons-text {
					top: 52% !important;
					-webkit-transform: translateY(-50%);
					transform: translateY(-50%);
					position: absolute;
					width: 100%;
				  padding: 0px 15px;
				}
				.tooltip-container {
					position: absolute;
					top: 10px;
					right: 10px;
					height: 25px;
					width: 25px;
					line-height: 1.9;
					font-size: 10pt;
					border-radius: 50%;
					max-height: 0;
					opacity: 0;
					transition:max-height .2s ease-in-out;
					transition:opacity .2s ease-in-out;
					@include breakpoint(sm2) {
						opacity: 1!important;
						border: 1px solid #fff;
						max-height: initial;
					}
					.tooltip {
						visibility: hidden;
						width: 250px;
						background-color: #757a82;
						color: #fff;
						text-align: center;
						border-radius: 2px;
						padding: 15px;
						font-size:10pt;
						font-weight:400;
				    position: absolute;
						text-align:left;
						right: 0;
						z-index: 1;
						text-transform:none;
					}
					&:hover .tooltip {
						visibility: visible;
						opacity: 1;
						@include breakpoint(sm2) {
							visibility: hidden;
							opacity: 0;
						}
					}
					&.active {
						.tooltip {
							visibility: visible;
							opacity: 1;
						}
					}
				}
			}
		}
	}
	#home-content {
		text-align:center;
		h1 {
			margin-bottom: 60px;
			@include breakpoint(xxs) {
				margin-bottom: 30px;
			}
		}
		#home-page-bottom {
			text-align:center;
			.home-page-bottom-links {
				margin-top:10px;
				&:first-child {
					margin-top:100px;
				}
			}
		}
	}
//Safety Audit Pages
	.safety-audit-2-section {
		.base-content-container {
			max-width:800px;
			margin:0 auto;
			text-align:left;
			ol, ul {
				padding-left: 30px;
				@include breakpoint(xs) {
					padding-left: 20px;
				}
			}
		}
	}
	.safety-audit-3-section {
		h3 {
			text-align:center;
			font-weight: 500;
			font-size: 22pt;
			@include breakpoint(xs) {
				font-size: 17pt;
			}
		}
		textarea {
			width:100%;
		}
		.form-radio-container {
			.form-radio-buttons-container {
				margin-bottom: 15px;
				.form-radio-label {
					max-width: 435px;
					@include breakpoint(xs) {
						max-width: 84%;
					}
				}
			}
		}
		.form-input select {	
			margin: 0 auto;
			display: block;
			margin-bottom: 30px;
		}
		.safety-audit-input-container {
			max-width: 481px;
			margin: 0 auto;
			display: block;
		
		}
		.safety-audit-checkboxes {
			padding-bottom: 10px;
			border-bottom: 2px solid #e6e7e8;
			margin-bottom: 30px;
			position:relative;
			&:last-child {
				border-bottom: none;
			}
			.safety-audit-anchor-links {
				position:absolute;
				top:-40px;
				@include breakpoint(sm) {
					top:-120px;
				}
				@include breakpoint(xs) {
					top:-110px;
				}
			}
		}
	}
	#safety-audit-menu-trigger {
		position:absolute;
		top:60px;
	}
	#safety-audit-menu {
		position:absolute;
		top:60px;
		left:auto;
		right: 10px;
		padding:10px 0px;
		max-width: 240px;
		border-radius: 0px 0px 2px 2px;
		max-width: 220px;
		@include breakpoint(sm2) {
			background-color: #fff;
			border: 2px solid #004b8d;
			border-radius: 4px;
			right: -220px;
		}
		@include breakpoint(sm) {
			top:150px!important;
		}
		&.active {
			right: 10px;
			#safety-audit-menu-button {
				display:none;
			}
			#safety-audit-menu-close {
				@include breakpoint(sm2) {
					display:block;
				}
			}
		}
		&.fixed {
			position:fixed;
			top:80px;
			@include breakpoint(xs) {
				top:100px;
			}
		}
		&.mobile {
			@include breakpoint(sm2) {
				position:fixed;
				top:100px;
			}
		}
		#safety-audit-menu-button {
			position:absolute;
			left:-50px;
			top: -2px;
			height:40px;
			width:50px;
			@include breakpoint(md2) {
				display:none;
			}
			#safety-audit-menu-button-link {
				cursor:pointer;
				display:block;
				height:100%;
				background-color: #fff;
				border: 2px solid $primary-color;
				border-radius: 5px 0px 0px 5px;
				i {
					font-size: 18pt;
					margin-left: 12px;
					margin-top: 5px;
				}
			}
		}
		#safety-audit-menu-title {
			font-size:18pt;
			font-weight:700;
			color:$primary-color;
			text-align:right;
			margin-bottom: 10px;
			margin-right: 10px;
			@include breakpoint(sm2) {
				text-align: center;
			}
		}
		.safety-audit-menu-link {
			display: inline-block;
			vertical-align: middle;
			display:block;
			font-size: 10pt;
			cursor:pointer;
			text-align: right;
			a {
				text-decoration:none;
			}
			.safety-audit-menu-link-text {
				display: inline-block;
				vertical-align: middle;
				width: 80%;
			}
			.safety-audit-menu-check {
				display: inline-block;
				vertical-align: middle;
				width: 15%;
				.safety-audit-menu-check-yn {
					border: 3px solid #e6e6e6;
					height: 20px;
					width: 20px;
					border-radius:50%;
					display: inline-block;
					vertical-align: middle;
					margin-right: 10px;
					margin-left: 5px;
					position: relative;
					&.applic {
						background-color: #00935B;
					}
					&.notap {
						background-color: #2D009C;
					}
					&.notin {
						background-color: #C8002F;
					}
				}
			}
		}
		.safety-audit-menu-option {
			margin-bottom: 5px;
			&:last-child {
				margin-bottom:0px;
			}
		}
		#safety-audit-menu-close {
			display:none;
			position:absolute;
			top: 5px;
			right: 7px;
			font-size: 16pt;
			cursor:pointer;
			color:$primary-color;
		}
	}

	
//Profile Page
	#profile-content {
		text-align: center;
		.profile-input-label {
				margin-bottom:10px;
				font-size:14pt;
				width: 49%;
				display: inline-block;
				text-align: right;
				padding-right: 10px;
				font-weight: 500;
				@include breakpoint(xs) {
					width: 100%;
					text-align: center;
					display: block;
				}
		}    
		.form-input {
			margin-bottom:20px;
			width: 49%;
			display: inline-block;
			text-align: left;
			padding-left: 10px;
			@include breakpoint(xs) {
				width: 100%;
				text-align: center;
				display: block;
			}
			input {
				border-radius:4px;
				border: 2px solid #e6e6e6;
			}
			select {
				border: 2px solid #e6e6e6;
				border-radius: 4px;
				padding: 5px;
				cursor: pointer;
				color:$primary-color;
				padding: 5px 8px;
				font-weight:bold;
				option {
					margin-bottom:10px;
					font-weight:bold;
				}
			}
			textarea {
				border: 2px solid #e6e6e6;
				border-radius: 4px;
				color:$primary-color;
				font-weight:bold;
			}
		}
	}
//Contact Page
//Profile Page
	#contact-content {
		text-align: center;
		.contact-input-label {
			margin-bottom:20px;
			font-size:14pt;
			width: 20%;
			display: inline-block;
			vertical-align: middle;
			text-align: right;
			padding-right: 10px;
			font-weight: 500;
			@include breakpoint(xs) {
				width: 100%;
				text-align: center;
				display: block;
				margin-bottom:10px;
			}
		}    
		.form-input {
			margin-bottom:20px;
			width: 49%;
			display: inline-block;
			vertical-align: middle;
			text-align: left;
			padding-left: 10px;
			@include breakpoint(md) {
