//Preloader
	#preloader {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #fff;
		z-index: 9999;
		text-align:center;
		#status {
				width: 300px;
				height: 300px;
				display:inline-block;
		}
		h1 {
			margin-top:20px;
			text-transform:uppercase;
			font-size: 50pt;
		}
		#preloader-container {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			margin-left: auto;
			margin-right: auto;
			left: 0;
			right: 0;
		}
	}
	
//Animations
	//ng-if
		.animate-if.ng-enter {
			-webkit-transition: max-height 1.3s, max-width .5s, opacity .7s;
			-moz-transition: max-height 1.3s, max-width .5s, opacity .7s;
			transition:  max-height 1.3s, max-width .5s, opacity .7s;
		}
		.animate-if.ng-leave {
			-webkit-transition: all .4s;
			-moz-transition: all .4s;
			transition: all .4s;
		}
		.animate-if.ng-enter,
		.animate-if.ng-leave.ng-leave-active {
			max-height:0;
			max-width:0;
			opacity:0;
		}
		.animate-if.ng-leave,
		.animate-if.ng-enter.ng-enter-active {
			max-height: 1000px;
			max-width:500px;
			opacity:1;
		}
	//ng-view
		.animate-view.ng-enter {
			-webkit-transition: opacity .3s ease;
			-moz-transition: opacity .3s ease;
			transition:  opacity .3s ease;
		}
		.animate-view.ng-leave {
			-webkit-transition: opacity 0s ease;
			-moz-transition: opacity 0s ease;
			transition: opacity 0s ease;
		}
		.animate-view.ng-enter,
		.animate-view.ng-leave.ng-leave-active {
			opacity:0;
		}
		.animate-view.ng-leave,
		.animate-view.ng-enter.ng-enter-active {
			opacity:1;
		}

